import { withSelect } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { useState, useEffect, useRef, render } from '@wordpress/element';
import { Spinner } from '@wordpress/components';

import Settings from './Settings';
import Style from './Style';
import Gallery from './Gallery';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { gallery, slideSpeed, columns } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	const [activeIndex, setActiveIndex] = useState(null);
	const bigbImageGallery = useRef(null);

	useEffect(() => { setActiveIndex(!isSelected || !gallery.length ? null : 0); }, [isSelected]); // Set default activeIndex

	const renderGallery = () => render(
		<Gallery gallery={gallery} slideSpeed={slideSpeed} columns={columns} isPopup={false} editActiveIndex={activeIndex} setEditActiveIndex={setActiveIndex} />,
		bigbImageGallery.current
	);

	useEffect(() => {
		renderGallery();
	}, [gallery, !gallery.length, columns, bigbImageGallery.current, activeIndex]);

	useEffect(() => {
		render(
			<p><Spinner /> Loading...</p>,
			bigbImageGallery.current
		);

		setTimeout(() => {
			renderGallery();
		}, 0);
	}, [gallery.length, ...gallery.map(g => g.images?.length), slideSpeed, bigbImageGallery.current]);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} activeIndex={activeIndex} setActiveIndex={setActiveIndex} />

		<div className={className} id={`bigbImageGallery-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			{/* {`#bigbImageGallery-${clientId} .bigbImageGallery .popupGallery{
				padding: ${isEditorSidebarOpened ? '65px 305px 30px 30px' : '65px 30px 30px 30px'}
			}`} */}

			<div className='bigbImageGallery' ref={bigbImageGallery}></div>
			{!gallery.length && <h3 className='addGalleryFirst'>{__('Please add a gallery item first!')}</h3>}
		</div >
	</>;
};
export default withSelect((select) => {
	return {
		isEditorSidebarOpened: select('core/edit-post').isEditorSidebarOpened()
	};
})(Edit);