import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, TextControl, RangeControl, ToolbarGroup, ToolbarButton, __experimentalUnitControl as UnitControl, Tooltip, Button, Dashicon } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import BDevice from '../../Components/BDevice';
import Background from '../../Components/Background';
import { InlineMediaUpload } from '../../Components/MediaControl';
import ShadowControl from '../../Components/ShadowControl';
import ColorsControl from '../../Components/ColorsControl';
import Typography from '../../Components/Typography';

import icons from './Const/icons';
import options from './Const/options';
const { generalStyleTabs, pxUnit, perUnit, emUnit } = options;

const Settings = ({ attributes, setAttributes, activeIndex, setActiveIndex }) => {
	const { gallery, slideSpeed, itemHeight, columns, columnGap, rowGap, background, shadow, hoverShadow, titleTypo, subtitleTypo } = attributes;

	const [device, setDevice] = useState('desktop');

	const addGalleryItem = () => {
		setAttributes({
			gallery: [...gallery, {
				title: `Title ${gallery.length + 1}`,
				subtitle: `Subtitle ${gallery.length + 1}`,
				images: [],
				colors: {
					color: '#fff',
					bgType: 'solid',
					bg: '#505a64d9'
				},
				btnColors: {
					color: '#fff',
					bgType: 'solid',
					bg: '#000a1480'
				}
			}]
		});
		setActiveIndex(gallery.length);
	}

	const updateGallery = (type, val, otherIndex = false) => {
		const newGallery = [...gallery];

		if (false !== otherIndex) {
			newGallery[activeIndex][type][otherIndex] = val;
			setAttributes({ gallery: newGallery });
		} else {
			newGallery[activeIndex][type] = val;
			setAttributes({ gallery: newGallery });
		}
	}

	const { title = '', subtitle = '', images = [], colors = {}, btnColors = {} } = gallery[activeIndex] || {};

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' === tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'image-gallery')}>
						<div className='helpItem'>
							<a href='https://bblockswp.com/docs/3d-image-gallery-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'image-gallery')}</a>
						</div>

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/3d-image-gallery/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'image-gallery')}</span>
								<span>{__('We are new and we need your help to grow!🙏', 'image-gallery')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody addRemoveItems' title={__('Add or Remove Items', 'image-gallery')}>
						{null !== activeIndex && <PanelBody className='bPlPanelBody editItem' title={__(`Item ${activeIndex + 1}:`, 'image-gallery')} initialOpen={true}>
							<Title mt='0'>{__('Title:', 'image-gallery')}</Title>
							<TextControl value={title} onChange={val => updateGallery('title', val)} />

							<Title>{__('Subtitle:', 'image-gallery')}</Title>
							<TextControl value={subtitle} onChange={val => updateGallery('subtitle', val)} />

							<Title>{__('Images:', 'image-gallery')}</Title>
							{images.map((img, i) => <PanelRow key={i}>
								<InlineMediaUpload value={img} type={['image']} onChange={val => updateGallery('images', val, i)} placeholder={__('Enter gallery image url', 'image-gallery')} />

								<Tooltip text={__('Remove this Image', 'image-gallery')} position='top center'>
									<Button onClick={e => {
										e.preventDefault();
										updateGallery('images', [...images.slice(0, i), ...images.slice(i + 1)]);
									}} ><Dashicon icon='no' /></Button>
								</Tooltip>
							</PanelRow>)}

							<div className='addItem'>
								<Button label={__('Add Image', 'image-gallery')} onClick={() => updateGallery('images', [...images, ''])}><Dashicon icon='plus' />{__('Add Image', 'image-gallery')}</Button>
							</div>

							<ColorsControl className='mt20' colors={colors} onChange={val => updateGallery('colors', val)} defaults={{ color: '#fff', bgType: 'solid', bg: '#505a64d9' }} />

							<ColorsControl label={__('Button Colors:', 'image-gallery')} colors={btnColors} onChange={val => updateGallery('btnColors', val)} defaults={{ color: '#fff', bgType: 'solid', bg: '#000a1480' }} />

							<PanelRow className='itemAction mt20'>
								<Button className='removeItem' label={__('Remove', 'image-gallery')} onClick={e => {
									e.preventDefault();
									setActiveIndex(1 === gallery.length ? null : 0 === activeIndex ? 0 : activeIndex - 1);
									setAttributes({ gallery: [...gallery.slice(0, activeIndex), ...gallery.slice(activeIndex + 1)] });
								}} ><Dashicon icon='no' />{__('Remove', 'image-gallery')}</Button>

								<Button className='duplicateItem' label={__('Duplicate', 'image-gallery')} onClick={e => {
									e.preventDefault();
									setActiveIndex(gallery.length);
									setAttributes({ gallery: [...gallery, { ...gallery[activeIndex] }] });
								}} >{icons.gearSettings}{__('Duplicate', 'image-gallery')}</Button>
							</PanelRow>
						</PanelBody>}

						<div className='addItem'>
							<Button label={__('Add New Gallery Item', 'image-gallery')} onClick={() => addGalleryItem()}><Dashicon icon='plus' />{__('Add New Gallery Item', 'image-gallery')}</Button>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Gallery Settings', 'image-gallery')} initialOpen={false}>
						<Title mt='0'>{__('Slide Speed (in seconds):', 'image-gallery')}</Title>
						<RangeControl value={slideSpeed} onChange={val => setAttributes({ slideSpeed: val })} min={0} max={10} step={0.01} beforeIcon='slides' />

						<UnitControl className='mt20' label={__('Item Height:', 'image-gallery')} labelPosition='left' value={itemHeight} onChange={val => setAttributes({ itemHeight: val })} units={[pxUnit, perUnit, emUnit]} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Layout Settings', 'image-gallery')} initialOpen={false}>
						<PanelRow>
							<Title mt='0'>{__('Columns:', 'image-gallery')}</Title>
							<BDevice device={device} onChange={val => setDevice(val)} />
						</PanelRow>
						<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

						<UnitControl className='mt20' label={__('Column Gap:', 'image-gallery')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: val })} units={[pxUnit, perUnit, emUnit]} />

						<UnitControl className='mt20' label={__('Row Gap:', 'image-gallery')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: val })} units={[pxUnit, perUnit, emUnit]} />
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Gallery', 'image-gallery')}>
						<Background background={background} onChange={val => setAttributes({ background: val })} defaults={{ type: 'gradient', gradient: 'linear-gradient(#ff5f6d, #ffc371)' }} isImage={false} />

						<ShadowControl label={__('Shadow:', 'image-gallery')} shadow={shadow} onChange={val => setAttributes({ shadow: val })} defaults={{ vOffset: '4px', blur: '4px', spreed: '0px', color: '#000a1459' }} />

						<ShadowControl label={__('Hover Shadow:', 'image-gallery')} shadow={hoverShadow} onChange={val => setAttributes({ hoverShadow: val })} defaults={{ vOffset: '12px', blur: '20px', spreed: '-12px', color: '#000a14cc' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Title & Subtitle', 'image-gallery')} initialOpen={false}>
						<Typography label={__('Title Typography', 'image-gallery')} typography={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: 48 }} />

						<Typography label={__('Subtitle Typography', 'image-gallery')} typography={subtitleTypo} onChange={val => setAttributes({ subtitleTypo: val })} defaults={{ fontSize: 20 }} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<ToolbarGroup className='bPlToolbar'>
				<ToolbarButton label={__('Add New Gallery Item', 'image-gallery')} onClick={() => addGalleryItem()}><Dashicon icon='plus' /></ToolbarButton>
			</ToolbarGroup>
		</BlockControls>
	</>;
};
export default Settings;